/** Media Queries **/
.active {
  background-color: #0034c8;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  display: inline-block;
  border: none;
  border-radius: 5px;
}
.active:hover {
  background-color: #002ba4;
  cursor: pointer;
}

.inactive {
  background-color: #616161;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  display: inline-block;
  border: none;
  border-radius: 5px;
}
.inactive:hover {
  background-color: #4f4f4f;
  cursor: pointer;
}

.boton-azul-block {
  background-color: #0034c8;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  display: block;
  border: none;
  border-radius: 5px;
}
.boton-azul-block:hover {
  background-color: #002ba4;
  cursor: pointer;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 160rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
  color: #FFFFFF;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Roboto", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 80rem;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.gallery {
  padding: auto 30rem;
}

.galeria-imagenes {
  padding: 0;
  list-style: none;
  display: grid;
  gap: 5rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .galeria-imagenes {
    display: grid;
    gap: 2.5rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .galeria-imagenes {
    display: grid;
    gap: 2.5rem;
    grid-template-columns: repeat(4, 1fr);
  }
}

.imagen img {
  transition: all 0.9s ease;
  -webkit-transition: all 0.9s ease;
  /* Safari y Chrome */
  -moz-transition: all 0.9s ease;
  /* Firefox */
  -o-transition: all 0.9s ease;
  /* IE 9 */
  -ms-transition: all 0.9s ease;
  /* Opera */
  width: 100%;
}

.imagen:hover img {
  -webkit-transform: scale(1.25);
  -moz-transform: scale(1.25);
  -ms-transform: scale(1.25);
  -o-transform: scale(1.25);
  transform: scale(1.25);
}

.imagen {
  overflow: hidden;
}

.selected {
  filter: grayscale(1);
  position: relative;
  transition: all 2s ease;
}

